import { NgModule } from '@angular/core';
import { RouterModule,ActivatedRoute} from '@angular/router';
import { SharedModule } from '../shared/shared.module';
import { FlexLayoutModule } from "@angular/flex-layout";

// import { SafePipe } from '../pipe/safe.pipe';

import { CollegeComponent } from './college.component';
import { CollegeDetailComponent } from './college-detail/college-detail.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CollegeListComponent,DialogOverviewExampleDialog } from './college-list/college-list.component';
import { CollegeListSummaryComponent, DialogEditNotesDialog } from './college-list-summary/college-list-summary.component';
import { CollegeListMapComponent } from './college-map/college-list-map.component';
import { GuidedSearchComponent,DialogSvaeCurrentSearchDialog } from './guided-search/guided-search.component';
import { MyChancesComponent } from './chances/mychances.component';
import { NetPriceEstimatesComponent } from './price-extimates/net-price-estimates.component';
import { SearchBar } from './shared/searchbar.component';
import { DialogConfirmDialog } from './shared/confirm.component';

import { ChartModule } from 'angular-highcharts';
import { DndModule } from 'ngx-drag-drop';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';

import { CollegeService } from '../service/college.service';
import { ChartService } from '../service/chart.service';
import { ScrollSpyModule, ScrollSpyService } from 'ngx-scrollspy';
import { ScrollSpyIndexModule } from 'ngx-scrollspy';
import { ScrollSpyAffixModule } from 'ngx-scrollspy';

@NgModule({
  imports: [
    SharedModule,
    ChartModule,
    DndModule,
    FlexLayoutModule,
    LeafletModule,

    ScrollSpyIndexModule,
    ScrollSpyAffixModule,
    ScrollSpyModule.forRoot(),
    
    RouterModule.forChild([
      {
        path: '',
        component: CollegeComponent,
        children: [
          {path:'',component:DashboardComponent},
          {path:'collegelist',component:CollegeListComponent},
          {path:'listsummary',component:CollegeListSummaryComponent},
          {path:'map',component:CollegeListMapComponent},
          {path:'search',component:GuidedSearchComponent},
          {path:'mychances',component:MyChancesComponent},
          {path:'costcompare',component:NetPriceEstimatesComponent},
          {path:'info/:id',component:CollegeDetailComponent}
        ]
      }
    ]
    )
  ],
  declarations: [
    CollegeComponent,
    CollegeDetailComponent,
    CollegeListComponent,
    DialogOverviewExampleDialog,
    DialogConfirmDialog,
    DialogEditNotesDialog,
    CollegeListMapComponent,
    CollegeListSummaryComponent,
    DashboardComponent,
    GuidedSearchComponent,
    DialogSvaeCurrentSearchDialog,
    MyChancesComponent,
    NetPriceEstimatesComponent,
    SearchBar,
  ],
  entryComponents: [
    DialogOverviewExampleDialog,DialogConfirmDialog,DialogEditNotesDialog,DialogSvaeCurrentSearchDialog
  ],
  providers:[CollegeService,ChartService,ScrollSpyService]
})
export class CollegeModule { }
