import { Component, OnInit, ElementRef, ViewChild, Renderer2} from '@angular/core';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import { CollegeService } from "../../service/college.service";

@Component({
  selector: 'app-net-price-estimates',
  templateUrl: './net-price-estimates.component.html',
  styleUrls: ['./net-price-estimates.component.css']
})
export class NetPriceEstimatesComponent implements OnInit {

  // collegesDataFromServer:any={
  //   mycolleges:[],
  //   interested:[]
  // }

  colleges = [];
  optionArray = {mycolleges:[],interested:[]};
  @ViewChild('collegelist') canRoll: ElementRef;
  showAlert=false;
  step=191;
  showArrow = {
    l:false,
    r:false,
  };
  constructor(
    private render:Renderer2,
    public collegeService:CollegeService
  ) { 
    this.collegeService.getCompareList().then(data=>{
      console.log(data)
      this.sortArr(data.mycolleges);
      this.sortArr(data.interested);
      this.optionArray = data;
    })
    // this.getOptionArray();
  }

  
  sortArr(data){
    
    data.sort((a,b)=>{
      let nameA = a.name.toUpperCase();
      let nameB = b.name.toUpperCase();
      if (nameA < nameB){return -1;}
      if (nameA > nameB){return 1;}
      return 0;
    });

  }



  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.colleges, event.previousIndex, event.currentIndex);
  }

  ngOnInit() {
    
  }
  initArrowStatus(){
    let list = this.canRoll.nativeElement;
    let n = this.colleges.length;
    
    if(n>4){
      // this.showArrow.r=true;
      this.showArrow.r = list.offsetLeft > this.step*(-1)*(n-4) ?  true : false ;
      // n*this.step+list.offsetLeft<=this.step*4 ? this.showArrow.r=false :this.showArrow.r=true ;
      list.offsetLeft<0 ? this.showArrow.l = true : this.showArrow.l=false;
    }else{
      this.showArrow.r=this.showArrow.l=false;
    }
  }
  selectOptRest=null;
  //select and add college to colleges array
  selectCollege(w,e){
    let v = Number(e.value);
    if(w===0){
      if(v<0){
        let items = this.optionArray.mycolleges
        for(let i=0; i<items.length; i++){
          if(items[i].isdata && this.colleges.indexOf(items[i])==-1){this.colleges.push(items[i])}
        }
      }else{
        let item = this.findDataFromMC(v);
        item.isdata?
          this.colleges.indexOf(item)!=-1 ? null : this.colleges.push(item) 
        :this.showAlert=true;
      }
      
    }else if(w===1){
      if(v<0){
        let items = this.optionArray.interested
        for(let i=0; i<items.length; i++){
          if(items[i].isdata && this.colleges.indexOf(items[i])==-1){this.colleges.push(items[i])}
        }
      }else{
        let item = this.findDataFromIC(v);
        item.isdata?
          this.colleges.indexOf(item)!=-1 ? null : this.colleges.push(item) 
        :this.showAlert=true;
      }
    }
    this.selectOptRest=Math.random();
    this.initArrowStatus();
  }

  move(d){
    
    let list2 = this.canRoll.nativeElement;
    let distance = list2.offsetLeft+d*this.step + 'px'
    this.render.setStyle(this.canRoll.nativeElement, 'left', distance);
    
    this.initArrowStatus();
    console.log(list2.offsetWidth,list2.offsetLeft)
  }
  findDataFromMC(v){
    return this.optionArray.mycolleges.find(function(x) {return x.programID===v});
  }
  findDataFromIC(v){
    return this.optionArray.interested.find(function(x) {return x.programID===v })
  }
  // getOptionArray(){
  //   this.optionArray = this.collegesDataFromServer
  // }
  counter(i: any) {
    return new Array(i);
  }

}
