  loadMap(){
    // Data of map
    this.mapoptions = {
      layers: [
        tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', { maxZoom: 18, detectRetina: true, attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors' }),
        marker([this.data.map[1].data.location.Latitude, this.data.map[1].data.location.Longitude], {
          icon: icon({
            iconSize: [25, 41],
            iconAnchor: [13, 41],
            iconUrl: 'leaflet/marker-icon.png',
            shadowUrl: 'leaflet/marker-shadow.png',
            popupAnchor: [0, -41]
          })
        }).bindPopup(this.data.map[1].data.address)
      ],
      zoom: 15,
      center: latLng(this.data.map[1].data.location.Latitude, this.data.map[1].data.location.Longitude)
    }
  }
  getMonth(num) {
    let month = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
    return month[num - 1]
  }
  counter(i: any) {
    return new Array(i);
  }

  scrollToTop(){
    scrollTo(0,150);
  }
  removeFormList(data){
    console.log(data);
  }
  public goTo(anchor: string) {
    setTimeout(() => {
      (<any>document).querySelector('#' + anchor).scrollIntoView();
    });
  }
  openConfirm(e):void{
    const dialogRef = this.dialog.open(DialogConfirmDialog, {
      width: '400px',
      data: {name:"This message",id:e.id}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log(result);
      result?this.removeDiscuss(result):''
    });
  }
