import { Component, OnInit, } from '@angular/core';
import { icon, latLng, marker, layerGroup, tileLayer, map} from 'leaflet';
import { CollegeService } from "../../service/college.service";

@Component({
  selector: 'app-college-list-map',
  templateUrl: './college-list-map.component.html',
  styleUrls: ['./college-list-map.component.css']
})
export class CollegeListMapComponent implements OnInit {
  icons={
    mycollegeIcon : icon({
      iconSize: [ 25, 41 ],
      iconAnchor: [ 13, 41 ],
      iconUrl: '/assets/img/location_my_colleges.png',
      shadowUrl: 'leaflet/marker-shadow.png',
      popupAnchor:[-1,-36]
    }),
    interestIcon : icon({
      iconSize: [ 25, 41 ],
      iconAnchor: [ 13, 41 ],
      iconUrl: '/assets/img/location_interested_colleges.png',
      shadowUrl: 'leaflet/marker-shadow.png',
      popupAnchor:[-1,-36]
    }),
    notInterestIcon : icon({
      iconSize: [ 25, 41 ],
      iconAnchor: [ 13, 41 ],
      iconUrl: '/assets/img/location_no_interestd_colleges.png',
      shadowUrl: 'leaflet/marker-shadow.png',
      popupAnchor:[-1,-36]
    })
  }
  
  markers0 = [];
  markers1 = [];
  markers2 = [];
  // Define our base layers so we can reference them multiple times
  streetMaps = tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    detectRetina: true,
    attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
  });

  panIcon=icon({
    iconSize: [ 25, 41 ],
    iconAnchor: [ 13, 41 ],
    iconUrl: 'leaflet/marker-icon.png',
    shadowUrl: 'leaflet/marker-shadow.png'
  })
  
  // Layers control object with our two base layers and the three overlay layers
  layersControl:any={
    
  };
  options={
    layers: [ this.streetMaps,layerGroup(this.markers0),layerGroup(this.markers1)],
    zoom: 4,
    center: latLng(40, -109.726909)
  };
  
  constructor( public collegeService:CollegeService ) { 
    this.collegeService.getCollegeMapList().then(result=>{
      this.initData(result);
    })
  }

  initData(data:any){
    if(data['1']){
      for(let i=0; i<data['1'].length; i++){
        if(data['1'][i].info.latitude && data['1'][i].info.longitude){
          this.markers0.push(marker([ data['1'][i].info.latitude, data['1'][i].info.longitude ], {icon:this.icons.mycollegeIcon}).bindPopup('<b>'+data['1'][i].info.name+'</b><br/>'+data['1'][i].info.address))
        }
      }
    }
    if(data['2']){
      for(let i=0; i<data['2'].length; i++){
        if(data['2'][i].info.latitude && data['2'][i].info.longitude){
          this.markers1.push(marker([ data['2'][i].info.latitude, data['2'][i].info.longitude ], {icon:this.icons.interestIcon})
          .bindPopup('<b>'+data['2'][i].info.name+'</b><br/>'+data['2'][i].info.address))
        }
      }
    }
    if(data['3']){
      for(let i=0; i<data['3'].length; i++){
        if(data['3'][i].info.latitude && data['3'][i].info.longitude){
          this.markers2.push(marker([ data['3'][i].info.latitude, data['3'][i].info.longitude ], {icon:this.icons.notInterestIcon})
          .bindPopup('<b>'+data['3'][i].info.name+'</b><br/>'+data['3'][i].info.address))
        }
      }
    }
    let collegeGroup={
      mycolleges:layerGroup(this.markers0),
      interest:layerGroup(this.markers1),
      notInterest:layerGroup(this.markers2),
    }
    data['1']?this.options.layers['1']=collegeGroup.mycolleges:'';
    data['2']?this.options.layers['2']=collegeGroup.interest:'';
    // data['3']?this.options.layers['3']=collegeGroup.notInterest:'';

    this.layersControl={
      overlays: {
        '<span style="height:20px;background:url(/assets/img/location_my_colleges.png) no-repeat;background-size:contain;display:inline-block; padding:3px 0 0 16px;">My Colleges</span>': collegeGroup.mycolleges,
        '<span style="height:20px;background:url(/assets/img/location_interested_colleges.png) no-repeat;background-size:contain;display:inline-block; padding:3px 0 0 16px;">Interested Colleges</span>': collegeGroup.interest,
        '<span style="height:20px;background:url(/assets/img/location_no_interestd_colleges.png) no-repeat;background-size:contain;display:inline-block; padding:3px 0 0 16px;">Not Interested Colleges</span>': collegeGroup.notInterest
      }
    };
    
    console.log(data);
  }
  
  ngOnInit() {
    
  }

}
