import { Component, OnInit, } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { ChartService } from '../../service/chart.service';

import { CollegeService } from "../../service/college.service";

@Component({
  selector: 'app-mychances',
  templateUrl: './mychances.component.html',
  styleUrls: ['./mychances.component.css', '../college-list-summary/college-list-summary.component.css', '../dashboard/dashboard.component.css']
})
export class MyChancesComponent implements OnInit {
  color: any = {};//colors about bars texts exc.
  credit: any;
  actchart: any = {};
  satchart: any;
  actdisplayedColumns = ['label', 'a', 'b', 'c', 'd', 'e', 'myScores', 'collegeMiddle'];

  tabnumber: number = 0; //0 for MyColleges; 1 for Interested Colleges
  myChancesData: any = {
    myCollege: [],
    myCollegeChart:{},
    interested: [],
    interestedChart:{},
    myscore: {}
  }
  pageData: any;
  
  allExpandState: any = {
    first: true,
    anthers: false
  }
  checkValue: any = {
    sat: true,
    act: true,
    sc: true,
    sd: true,
  }
  disableClick: any = {
    legendItemClick: function () {
      return false;
    }
  }
  savedFlag={
    loading:false,
    state:false
  };
  constructor(public chartService: ChartService, public collegeService: CollegeService) {
    this.collegeService.getMyChancesList().then(result => {
      this.myChancesData.myscore = result.myscore;
      this.myChancesData.myCollege = result.mycollegelist;
      this.initData(result.mycollegelist,0);
      this.myChancesData.interested = result.interestedlist;
      this.initData(result.interestedlist,1);
      // console.log(this.myChancesData, result);
      this.changeTab(0);
    })

    this.color.barcolors = this.chartService.chart.barcolors;
    this.color.colors = this.chartService.chart.colors;
    this.color.maincolors = this.chartService.chart.maincolors;
    this.color.legendcolor = this.chartService.chart.legendcolor;

    this.credit = this.chartService.chart.credit;

    this.actchart = this.chartService.myChancesData.actChart;
    this.satchart = this.chartService.myChancesData.satChart;

  }

  ngOnInit() {
    this.changeTab(0);
  }
  changeTab(id: number) {
    this.tabnumber = id;
    id == 0 ? this.pageData = this.myChancesData.myCollege : this.pageData = this.myChancesData.interested
  }
 
  expand(s: boolean) {
    this.allExpandState.anthers = s;
    this.allExpandState.first = s;
  }
  
  editCollege(id,expect){
    this.savedFlag.loading=true;
    let post={expectation:expect}
    this.collegeService.editCollege(id,post).then(result=>{
      this.savedFlag.loading=false;
      result?this.savedFlag.state=true:''
      setTimeout(() => {
        this.savedFlag.state=false
      }, 2000);
    })
  }
  reDraw(){
    if(this.checkValue.sc){
      if(this.checkValue.act && this.checkValue.sat){
        this.checkValue.act=false
        this.checkValue.sat=false
        setTimeout(()=>{
          this.checkValue.act=true
          this.checkValue.sat=true
        })
      }
      if(!this.checkValue.act && this.checkValue.sat){
        this.checkValue.sat=false
        this.checkValue.act=false
        setTimeout(()=>{this.checkValue.sat=true})
      }
      if(this.checkValue.act && !this.checkValue.sat){
        this.checkValue.sat=false
        this.checkValue.act=false
        setTimeout(()=>{this.checkValue.act=true})
      }
    }
    
  }
  initData(data,id){
    let myChart: any = {
      type: 'bar',
      height: 200,
      spacingBottom: 55
    }
    let myLegend: any = {
      align: 'center',
      x: 0,
      verticalAlign: 'bottom',
      y: 30,
      reversed: true,
      floating: true,
      symbolRadius: 3,
      symbolHeight: 12,
      itemStyle: {
        color: this.color.legendcolor,
        fontSize: '10px',
        cursor: 'default'
      },
      shadow: false
    }
    let myPlotOptions: any = {
      series: {
        stacking: 'normal',
        events: this.disableClick,
      },
      line: {
        dataLabels: {
          enabled: true,
          y: 11,
          x: -16,
          style: { color: this.color.legendcolor, fontSize: '10px', fontWeight: 'bold', textOutline: 'none' }
        },
        enableMouseTracking: false,
        lineWidth: 0,
        marker: {
          lineWidth: 2,
          lineColor: 'white',
          fillColor: this.color.barcolors[4],
        },
        events: this.disableClick,
      },
      scatter: {
        dataLabels: {
          enabled: true,
          y: 11,
          x: -16,
          style: { color: this.color.legendcolor, fontSize: '10px', textOutline: 'none' }
        },
        enableMouseTracking: false,
        marker: {
          fillColor: this.color.barcolors[3],
        },
        lineWidth: 0,
      }
    }
    let myTooltip: any = {
      headerFormat: '<b>{point.x}</b><br/>',
      pointFormat: '{series.name}<br/>Total: {point.stackTotal}'
    }

    for (let i = 0; i < data.length; i++) {
      id==0
      ? this.myChancesData.myCollege[i].admissionsExpectation = this.myChancesData.myCollege[i].admissionsExpectation.toString()
      : this.myChancesData.interested[i].admissionsExpectation = this.myChancesData.interested[i].admissionsExpectation.toString()
      // ACT
      if (
        data[i].ACT.composite.scoreOf75thPercentile ||
        data[i].ACT.english.scoreOf75thPercentile ||
        data[i].ACT.math.scoreOf75thPercentile ||
        data[i].ACT.composite.scoreOf25thPercentile ||
        data[i].ACT.english.scoreOf25thPercentile ||
        data[i].ACT.math.scoreOf25thPercentile 
      ) {
        // act chart
        let myActChart = new Chart()
         myActChart = new Chart({
          chart: myChart,
          legend: myLegend,
          tooltip: myTooltip,
          plotOptions: myPlotOptions,
          colors: this.color.barcolors,
          series: [{
            name: 'Top 25%',
            data: [
              36 - data[i].ACT.composite.scoreOf75thPercentile,
              36 - data[i].ACT.english.scoreOf75thPercentile,
              36 - data[i].ACT.math.scoreOf75thPercentile],
          }, {
            name: 'Middle 50%',
            data: [
              data[i].ACT.composite.scoreOf75thPercentile - data[i].ACT.composite.scoreOf25thPercentile,
              data[i].ACT.english.scoreOf75thPercentile - data[i].ACT.english.scoreOf25thPercentile,
              data[i].ACT.math.scoreOf75thPercentile - data[i].ACT.math.scoreOf25thPercentile],
          }, {
            name: 'Bottom 25%',
            data: [
              data[i].ACT.composite.scoreOf25thPercentile,
              data[i].ACT.english.scoreOf25thPercentile,
              data[i].ACT.math.scoreOf25thPercentile]
          }, {
            type: 'scatter',
            name: 'Average',
            data: [
              data[i].ACT.composite.average,
              data[i].ACT.english.average,
              data[i].ACT.math.average,
            ],
          }, {
            type: 'line',
            name: 'My Superscore',
            data: [
              this.myChancesData.myscore.ACT.Composite,
              this.myChancesData.myscore.ACT.English,
              this.myChancesData.myscore.ACT.Math],
          }],
          title: { text: null },
          xAxis: {
            categories: this.actchart.categories
          },
          yAxis: {
            min: 14,
            max: 36,
            title: { text: null },
            allowDecimals: false,
            tickAmount: 12,
          },
          credits: this.credit,
        })
        
        // act table
        let myActTable: table[] = [
          {
            label: "Composite",
            a: data[i].ACT.composite.range5 ? data[i].ACT.composite.range5 + '%':'-',
            b: data[i].ACT.composite.range4 ? data[i].ACT.composite.range4 + '%':'-',
            c: data[i].ACT.composite.range3 ? data[i].ACT.composite.range3 + '%':'-',
            d: data[i].ACT.composite.range2 ? data[i].ACT.composite.range2 + '%':'-',
            e: data[i].ACT.composite.range1 ? data[i].ACT.composite.range1 + '%':'-',
            myScores: this.myChancesData.myscore.ACT.Composite ? this.myChancesData.myscore.ACT.Composite :'-',
            collegeMiddle: data[i].ACT.composite.scoreOf25thPercentile ? data[i].ACT.composite.scoreOf25thPercentile + " - " + data[i].ACT.composite.scoreOf75thPercentile :'-'
          },
          {
            label: "English",
            a: data[i].ACT.english.range5 ? data[i].ACT.english.range5 + '%':'-',
            b: data[i].ACT.english.range4 ? data[i].ACT.english.range4 + '%':'-',
            c: data[i].ACT.english.range3 ? data[i].ACT.english.range3 + '%':'-',
            d: data[i].ACT.english.range2 ? data[i].ACT.english.range2 + '%':'-',
            e: data[i].ACT.english.range1 ? data[i].ACT.english.range1 + '%':'-',
            myScores: this.myChancesData.myscore.ACT.English ? this.myChancesData.myscore.ACT.English : '-',
            collegeMiddle: data[i].ACT.english.scoreOf25thPercentile ? data[i].ACT.english.scoreOf25thPercentile + " - " + data[i].ACT.english.scoreOf75thPercentile : '-'
          },
          {
            label: "Math",
            a: data[i].ACT.math.range5 ? data[i].ACT.math.range5 + '%' : '-',
            b: data[i].ACT.math.range4 ? data[i].ACT.math.range4 + '%' : '-',
            c: data[i].ACT.math.range3 ? data[i].ACT.math.range3 + '%' : '-',
            d: data[i].ACT.math.range2 ? data[i].ACT.math.range2 + '%' : '-',
            e: data[i].ACT.math.range1 ? data[i].ACT.math.range1 + '%' : '-',
            myScores: this.myChancesData.myscore.ACT.Math ? this.myChancesData.myscore.ACT.Math :'-',
            collegeMiddle: data[i].ACT.math.scoreOf25thPercentile ? data[i].ACT.math.scoreOf25thPercentile + " - " + data[i].ACT.math.scoreOf75thPercentile : '-'
          }
        ];
       
        if(id==0){
          // this.myChancesData.myCollegeChart.act.push(myActChart);
          // this.myChancesData.myCollegeChart.acttable.push(myActTable);
          this.myChancesData.myCollege[i].act = myActChart;
          this.myChancesData.myCollege[i].acttable = myActTable;
        }else if(id==1){
          // this.myChancesData.interestedChart.act.push(myActChart);
          // this.myChancesData.interestedChart.acttable.push(myActTable);
          this.myChancesData.interested[i].act = myActChart;
          this.myChancesData.interested[i].acttable = myActTable;
        }
      } else {
        if(id==0){
          // this.myChancesData.myCollegeChart.act.push(false);
          // this.myChancesData.myCollegeChart.acttable.push(false);
          this.myChancesData.myCollege[i].act = false;
          this.myChancesData.myCollege[i].acttable = false;
        }else if(id==1){
          // this.myChancesData.interestedChart.act.push(false);
          // this.myChancesData.interestedChart.acttable.push(false);
          this.myChancesData.interested[i].act = false;
          this.myChancesData.interested[i].acttable = false;
        }
      }
      // SAT
      if (
        data[i].SAT.criticalReadingScores.scoreOf75thPercentile ||
        data[i].SAT.criticalReadingScores.scoreOf25thPercentile ||
        data[i].SAT.mathScores.scoreOf25thPercentile ||
        data[i].SAT.mathScores.scoreOf75thPercentile
        ) {
        // sat chart
        let mySatChart = new Chart({
          chart: myChart,
          legend: myLegend,
          tooltip: {
            headerFormat: '<b>{point.x}</b><br/>',
            pointFormat: '{series.name}<br/>Total: {point.stackTotal}'
          },
          plotOptions: myPlotOptions,
          colors: this.color.barcolors,
          series: [{
            name: 'Top 25%',
            data: [
              800 - data[i].SAT.criticalReadingScores.scoreOf75thPercentile,
              800 - data[i].SAT.mathScores.scoreOf75thPercentile,],
          }, {
            name: 'Middle 50%',
            data: [
              data[i].SAT.criticalReadingScores.scoreOf75thPercentile - data[i].SAT.criticalReadingScores.scoreOf25thPercentile,
              data[i].SAT.mathScores.scoreOf75thPercentile - data[i].SAT.mathScores.scoreOf25thPercentile],
          }, {
            name: 'Bottom 25%',
            data: [
              data[i].SAT.criticalReadingScores.scoreOf25thPercentile,
              data[i].SAT.mathScores.scoreOf25thPercentile,]
          }, {
            type: 'scatter',
            name: 'Average',
            data: [
              data[i].SAT.criticalReadingScores.average,
              data[i].SAT.mathScores.average,
            ],
          }, {
            type: 'line',
            name: 'My Superscore',
            data: [
              this.myChancesData.myscore.SAT.ReadingWriting,
              this.myChancesData.myscore.SAT.Math,],
          }],
          title: { text: this.satchart.subTitle },
          xAxis: {
            categories: this.satchart.categories,
            labels: {
              autoRotationLimit: 30
            }
          },
          yAxis: {
            min: 320,
            max: 800,
            title: {
              text: '  '
            },
            labels: {
              overflow: "allow",
            },
            allowDecimals: false,
            tickAmount: 13,
          },
          credits: this.credit,
        });
        
        // ast table
        let mySatTable: table[] = [
          {
            label: "EB Reading and Writing",
            a: data[i].SAT.criticalReadingScores.range5 ? data[i].SAT.criticalReadingScores.range5 + '%' : '-',
            b: data[i].SAT.criticalReadingScores.range4 ? data[i].SAT.criticalReadingScores.range4 + '%' : '-',
            c: data[i].SAT.criticalReadingScores.range3 ? data[i].SAT.criticalReadingScores.range3 + '%' : '-',
            d: data[i].SAT.criticalReadingScores.range2 ? data[i].SAT.criticalReadingScores.range2 + '%' : '-',
            e: data[i].SAT.criticalReadingScores.range1 ? data[i].SAT.criticalReadingScores.range1 + '%' : '-',
            myScores: this.myChancesData.myscore.SAT.ReadingWriting ? this.myChancesData.myscore.SAT.ReadingWriting : '-',
            collegeMiddle: data[i].SAT.criticalReadingScores.scoreOf25thPercentile ? data[i].SAT.criticalReadingScores.scoreOf25thPercentile + " - " + data[i].SAT.criticalReadingScores.scoreOf75thPercentile : '-'
          },
          {
            label: "Math",
            a: data[i].SAT.mathScores.range5 ? data[i].SAT.mathScores.range5 + '%' : '-',
            b: data[i].SAT.mathScores.range4 ? data[i].SAT.mathScores.range4 + '%' : '-',
            c: data[i].SAT.mathScores.range3 ? data[i].SAT.mathScores.range3 + '%' : '-',
            d: data[i].SAT.mathScores.range2 ? data[i].SAT.mathScores.range2 + '%' : '-',
            e: data[i].SAT.mathScores.range1 ? data[i].SAT.mathScores.range1 + '%' : '-',
            myScores: this.myChancesData.myscore.SAT.Math ? this.myChancesData.myscore.SAT.Math : '',
            collegeMiddle: data[i].SAT.mathScores.scoreOf25thPercentile ? data[i].SAT.mathScores.scoreOf25thPercentile + " - " + data[i].SAT.mathScores.scoreOf75thPercentile : ''
          }
        ]
        if(id==0){
          // this.myChancesData.myCollegeChart.sat.push(mySatChart);
          // this.myChancesData.myCollegeChart.sattable.push(mySatTable);
          this.myChancesData.myCollege[i].sat = mySatChart;
          this.myChancesData.myCollege[i].sattable = mySatTable;
        }else if(id==1){
          // this.myChancesData.interestedChart.sat.push(mySatChart);
          // this.myChancesData.interestedChart.sattable.push(mySatTable);
          this.myChancesData.interested[i].sat = mySatChart;
          this.myChancesData.interested[i].sattable = mySatTable;

        }
      } else {
        if(id==0){
          // this.myChancesData.myCollegeChart.sat.push(false);
          // this.myChancesData.myCollegeChart.sattable.push(false);
          this.myChancesData.myCollege[i].sat = false;
          this.myChancesData.myCollege[i].sattable = false;
        }else if(id==1){
          // this.myChancesData.interestedChart.sat.push(false);
          // this.myChancesData.interestedChart.sattable.push(false);
          this.myChancesData.interested[i].sat = false;
          this.myChancesData.interested[i].sattable = false;
        }
      }
      // GPA chart
      if (
        data[i].GPA.range1 ||
        data[i].GPA.range2 ||
        data[i].GPA.range3 ||
        data[i].GPA.range4 ||
        data[i].GPA.range5 ||
        data[i].GPA.range6 ) {
        let myGpaChart = new Chart({
          chart: {
            height: 270,
          },
          title: {
            text: 'My <br>Unweighted<br> GPA: ' + this.myChancesData.myscore.GPA,
            align: 'center',
            style: { fontSize: "16px" },
            verticalAlign: 'middle',
            y: -15
          },
          tooltip: {
            pointFormat: '{series.name}: <b>{point.percentage:.0f}%</b>'
          },
          plotOptions: {
            pie: {
              dataLabels: {
                enabled: true,
                format: '{point.name}<br><b style="font-size:14px;color:#a8b7c7">{point.y}%</b>',
                distance: 20,
                style: {
                  fontWeight: 'bold',
                  textOutline: "0px",
                  color: this.color.maincolors[1]
                }
              },
              size: '85%'
            }
          },
          series: [{
            type: 'pie',
            name: 'Students Admitted',
            innerSize: '70%',
            data: [
              ['3.75+', data[i].GPA.range1],
              ['3.50-3.74', data[i].GPA.range2],
              ['3.25-3.49', data[i].GPA.range3],
              ['3.00-3.24', data[i].GPA.range4],
              ['2.50-2.99', data[i].GPA.range5],
              ['under 2.49', data[i].GPA.range6]
            ]
          }],
          colors: this.color.colors,
          credits: this.credit,
        });
        
        if(id==0){
          // this.myChancesData.myCollegeChart.gpa.push(myGpaChart);
          this.myChancesData.myCollege[i].gpa = myGpaChart;
        }else if(id==1){
          // this.myChancesData.interestedChart.gpa.push(myGpaChart);
          this.myChancesData.interested[i].gpa = myGpaChart;
        }
      } else {
        if(id==0){
          // this.myChancesData.myCollegeChart.gpa.push(false);
          this.myChancesData.myCollege[i].gpa = null;
        }else if(id==1){
          // this.myChancesData.interestedChart.gpa.push(false);
          this.myChancesData.interested[i].gpa = null;
        }
      }
    }

  }


}


export interface table {
  label: string;
  a: string;
  b: string;
  c: string;
  d: string;
  e: string;
  myScores: string;
  collegeMiddle: string
}
const TABLE_DATA: table[] = []