import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { MatSort, MatTableDataSource, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CollegeService } from '../../service/college.service';
import {DialogConfirmDialog} from '../shared/confirm.component';

@Component({
  selector: 'app-college-list-summary',
  templateUrl: './college-list-summary.component.html',
  styleUrls: ['./college-list-summary.component.css']
})
export class CollegeListSummaryComponent implements OnInit {
  tabnumber:number=0; //0:Mycolleges; 1:Interested colleges; 2:Not insterested colleges
  inputID='';
  displayedColumns: string[] = ['programListRank','admissionExpectationID', 'name', 'city', 'state', 'commonApp', 'coalition', 'testOptional', 'note', 'moveto'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);
  lists={
    mycolleges:new MatTableDataSource([]),
    insterested:new MatTableDataSource([]),
    unInsterest:new MatTableDataSource([])
  }
  @ViewChild(MatSort) sort: MatSort;

  btnDisable:any={
    btn:true,
    alert:false
  }
  constructor(
    public collegeService:CollegeService,
    public dialog:MatDialog,
  ) { 
    this.initData();
  }
  initData(){
    this.collegeService.getCollegeList().then(result=>{
      this.lists.mycolleges = this.objToArray(result.data[1]);
      this.lists.insterested = this.objToArray(result.data[2]);
      this.lists.unInsterest = this.objToArray(result.data[3]);
      this.getData(this.tabnumber)
      console.log(result)
    })
  }
  getid(e:any){
    this.inputID=e
    console.log(this.inputID)
  }
  addToMyList(type,id){
    this.collegeService.addToMyList(type,id).then(result=>{
      result?this.initData():result?this.initData():this.btnDisable.alert=true;setTimeout(()=>{this.btnDisable.alert=false},3000);;
    })
  }
  // 将接口数据整理成所需结构；
  objToArray(data){
    let arr=[];
    if(data){
      let l = Object.keys(data);
      for(let i=0; i<l.length; i++){
        arr.push(data[l[i]]);
      }
    }
    
    return( new MatTableDataSource(arr) );
  }
  ngOnInit() {

  }

  changeTab(id:number){
    this.tabnumber=id;
    this.getData(id);
  }

  getData(id:number){
    switch(id){
      case 0:
        this.getMycollegeData();
        break;
      case 1:
        this.getInterestedData();
        break;
      case 2:
        this.getNotInterestData();
        break;
      default:
    }
  }

  getMycollegeData(){
    this.dataSource=this.lists.mycolleges
    setTimeout(() => {
      this.dataSource.sort = this.sort;
    });
  }

  getInterestedData(){
    this.dataSource=this.lists.insterested
    setTimeout(() => {
      this.dataSource.sort = this.sort;
    });
  }

  getNotInterestData(){
    this.dataSource=this.lists.unInsterest
    setTimeout(() => {
      this.dataSource.sort = this.sort;
    });
  }

  setData(id){

  }

  moveTo(typeid,id){
    console.log(typeid,id);
    this.collegeService.collegeListMoveCollege(id,typeid,1).then(result=>{
      this.initData();
    }).catch(err=>{
      console.log(err)
    })
  }

  changExpect(e,id){
    let post={expectation:e.value}
    this.collegeService.editCollege(id,post).then(result=>{
      // this.savedFlag.loading=false;
      // result?this.savedFlag.state=true:''
      // setTimeout(() => {
      //   this.savedFlag.state=false
      // }, 2000);
    })
  }
  removeFormList(id){
    this.collegeService.removeFormMyCollegeList(id).then(result=>{
      this.initData();
    })
  }
  editCollege(id,expect,note){
    let post={expectation:expect,notes:note}
    this.collegeService.editCollege(id,post).then(result=>{
      this.initData();
    })
  }

  gotoUrl(){
    window.open('/college/info/'+this.inputID,'_blank')
  }
  openConfirm(e):void{
    const dialogRef = this.dialog.open(DialogConfirmDialog, {
      width: '400px',
      data: {name:e.name,id:e.ID}
    });

    dialogRef.afterClosed().subscribe(result => {
      result?this.removeFormList(result):''
    });
  }
  openEditNotes(e):void{
    const dialogRef = this.dialog.open(DialogEditNotesDialog, {
      width: '400px',
      data: {note:e.note,expect:e.admissionExpectationID,name:e.name,programID:e.programID}
    });
      
    dialogRef.afterClosed().subscribe(result => {
      if(result){
        if(result.note==''){
          result.note=' ';
        }
        result?this.editCollege(result.programID,result.expect,result.note):''
      }
      
    });
  }
}
export interface collegeSummary{
  ID:string;
  programListRank:number;
  admissionExpectationID:number;
  name:string;
  programID:number;
  city:string;
  state:string;
  commonApp:number;
  coalition:number;
  testOptional:number;
  note:string;
  // moveto:any;
}
const ELEMENT_DATA:collegeSummary[]=[]


@Component({
  selector: 'dialog-overview-example-dialog',
  template: `
  <h3 mat-dialog-title class="text-center" style="border-bottom:1px solid #ddd;margin-bottom:10px;padding-bottom:10px">Edit notes</h3>
  
  <div mat-dialog-content>
    <div class="pb-3"><b>Name:</b> {{data.name}}</div>
    <b>Application Note:</b>
    <p class="pt-2"><input type="textarea" class="w-100" [(ngModel)]="data.note"></p>
  
  </div>
  <div mat-dialog-actions class="d-flex justify-content-between">
    <button mat-button  (click)="onNoClick()">Cancel</button> <button mat-button color="primary"  [mat-dialog-close]="data">Save</button>
  </div>
`,
})
export class DialogEditNotesDialog {

  constructor(
    public dialogRef: MatDialogRef<DialogEditNotesDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}
export interface DialogData {
  ID:number;
  note:string;
  name:string;
  programID:number;
  admissionExpectationID:string;
  locationType:string;
}