import { Component, OnInit, } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { ChartService } from '../../service/chart.service';
import { CollegeService } from "../../service/college.service";


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  maincolors: any;
  colors: any;
  credit: any;
  flag=false;
  chatsData: any;
  myChances = new Chart();
  myCollegeList = new Chart();
  myCollegeUndergraduateSize = new Chart();
  interestedCollegeUndergraduateSize = new Chart();
  netPriceRatings = new Chart();

  constructor(public chartService: ChartService,public collegeService:CollegeService) {
    this.maincolors = this.chartService.chart.maincolors;
    this.colors = this.chartService.chart.colors;
    this.credit = this.chartService.chart.credit;
    // this.chatsData = this.chartService.dashbooard;
    this.collegeService.getDashboard().then(data=>{
      this.chatsData=data;
      console.log(this.chatsData);
      this.drawCharts();
    })
  }

  ngOnInit() {
    
  }

  drawCharts() {
    this.myCollegeList = new Chart({
      chart: {
        height: 270,
      },
      title: {
        text: ''
      },
      colors: this.maincolors,
      tooltip: {
        pointFormat: '<b>{point.percentage:.1f}%</b>'
      },
      plotOptions: {
        pie: {
          dataLabels: {
            enabled: true,
            format: '<b>{point.y}</b>',
            distance: -50,
            style: { fontSize: "16px", textOutline: "0px", fontWeight: "bold" }
          },
          showInLegend: true,
          startAngle: -90,
          endAngle: 90,
          center: ['50%', '75%'],
          size: '130%'
        }
      },
      legend: {
        align: 'center',
        x: 0,
        verticalAlign: 'bottom',
        y: -3,
        floating: true,
        symbolRadius: 3,
        symbolHeight: 16,
        itemStyle: {
          color: '#515974',
          fontWeight: 'bold',
          fontSize: '16',
        },
        shadow: false
      },
      series: [{
        type: 'pie',
        name: 'My College List',
        innerSize: '30%',
        data: this.chatsData.myCollegeList.series.data
      }],
      credits: this.credit,
    });

    this.myChances = new Chart({
      chart: {
        type: 'column',
        height: 270,
        spacingBottom: 55
      },
      legend: {
        align: 'center',
        x: 0,
        verticalAlign: 'bottom',
        y: 35,
        floating: true,
        symbolRadius: 3,
        symbolHeight: 16,
        itemStyle: {
          color: '#515974',
          fontWeight: 'bold',
          fontSize: '16',
        },
        shadow: false
      },
      tooltip: {
        headerFormat: '<b>{point.x}</b><br/>',
        pointFormat: '{series.name}: {point.y}<br/>Total: {point.stackTotal}'
      },
      plotOptions: {
        column: {
          stacking: 'normal',
          borderRadius: 3,
          dataLabels: {
            enabled: true,
            style: { textOutline: "0px" }
          }
        }
      },
      colors: this.maincolors,
      series: this.chatsData.mychances.series,
      title: { text: this.chatsData.mychances.subTitle },
      xAxis: {
        categories: this.chatsData.mychances.categories
      },
      yAxis: {
        min: 0,
        allowDecimals: false,
        title: {
          text: this.chatsData.mychances.yaxisTitle
        },
        tickAmount: 6
      },
      credits: this.credit,
    });

    this.myCollegeUndergraduateSize = new Chart({
      chart: {
        height: 270,
      },
      title: {
        text: this.chatsData.myCollegeUndergraduateSize.subTitle,
        y: 125,
        style: { fontSize: "16px" }
      },
      colors: this.colors,
      tooltip: {
        pointFormat: 'Number of Colleges<b>{point.y}</b>'
      },
      plotOptions: {
        pie: {
          dataLabels: {
            enabled: true,
            format: '{point.name}<br><b style="font-size:14px;color:gray">{point.y}</b>',
            distance: 15,
            style: { color: this.maincolors[1], fontSize: "12px", textOutline: "0px", fontWeight: "bold" }
          },
          showInLegend: false,
          center: ['50%', '40%'],
          size: '85%'
        }
      },
      series: [{
        type: 'pie',
        innerSize: '70%',
        data: this.chatsData.myCollegeUndergraduateSize.series.data
      }],
      credits: this.credit,
    });

    this.interestedCollegeUndergraduateSize = new Chart({
      chart: {
        height: 270,
      },
      title: {
        text: this.chatsData.interestedCollegeUndergraduateSize.subTitle,
        y: 125,
        style: { fontSize: "16px" }
      },
      colors: this.colors,
      tooltip: {
        pointFormat: 'Number of Colleges: <b>{point.y}</b>'
      },
      plotOptions: {
        pie: {
          dataLabels: {
            enabled: true,
            format: '{point.name}<br><b style="font-size:14px;color:gray">{point.y}</b>',
            distance: 15,
            style: { color: this.maincolors[1], fontSize: "12px", textOutline: "0px", fontWeight: "bold" }
          },
          showInLegend: false,
          center: ['50%', '40%'],
          size: '85%'
        }
      },
      series: [{
        type: 'pie',
        innerSize: '70%',
        data: this.chatsData.interestedCollegeUndergraduateSize.series.data
      }],
      credits: this.credit,
    });

    this.netPriceRatings = new Chart({
      chart: {
        type: 'column',
        height: 270,
        spacingBottom: 55
      },
      legend: {
        align: 'center',
        x: 0,
        verticalAlign: 'bottom',
        y: 35,
        floating: true,
        symbolRadius: 3,
        symbolHeight: 16,
        itemStyle: {
          color: '#515974',
          fontWeight: 'bold',
          fontSize: '16',
        },
        shadow: false
      },
      tooltip: {
        headerFormat: '<b>{point.x}</b><br/>',
        pointFormat: '{series.name}: {point.y}<br/>Total: {point.stackTotal}'
      },
      plotOptions: {
        column: {
          stacking: 'normal',
          borderRadius: 3,
          dataLabels: {
            enabled: true,
            style: { textOutline: "0px" }
          }
        }
      },
      colors: this.maincolors,
      series: this.chatsData.NetPriceRatings.series,
      title: { text: this.chatsData.NetPriceRatings.subTitle, style: { fontSize: "12px" } },
      xAxis: {
        categories: this.chatsData.NetPriceRatings.categories
      },
      yAxis: {
        allowDecimals: false,
        title: {
          text: this.chatsData.NetPriceRatings.yaxisTitle
        },
        tickAmount: 6
      },
      credits: this.credit,
    });
    this.flag=true;
  }
}
