import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {FormControl} from '@angular/forms';
import { CollegeService } from '../../service/college.service';
 
@Component({
  selector: 'app-college-searchbar',
  styleUrls:['../college.css','./searchbar.component.css'],
  templateUrl: './searchbar.component.html',
})
export class SearchBar implements OnInit {
  @Output() clickSearch = new EventEmitter<string>();
  doSearch(){
    this.clickSearch.next(this.school_name);
  }
  @Output() changeInput = new EventEmitter<string>();
  callParent(){ 
    this.changeInput.next(this.id);
  }
  @Input('width') panleWidth:any;
  @Input('value') inputvalue:any;
  college_list=[];
  id='';
  school_name='';
  nameControl: FormControl = new FormControl();
    constructor(public collegeService:CollegeService) { 
      
    }
  
    ngOnInit() {
    }
    updateCollegeList(){
      this.id=null;
      this.collegeService.getSchoolNameList(this.school_name).then(result=>{
        this.college_list = result;
      })
    }
    setCollege(college:any){
      this.school_name = college.name;
      this.id = college.id;
    }
}