import { Component, OnInit, ViewChild, Inject, ElementRef, Renderer2, HostListener, ViewChildren, QueryList } from '@angular/core';
import { CollegeService } from '../../service/college.service';
import { AuthService } from '../../service/auth.service';
import {MatSort, MatTableDataSource, MatDialog, MatDialogRef, MAT_DIALOG_DATA,MatOption, MatSelect} from '@angular/material';
import {FormControl, Validators, NgModel} from '@angular/forms';
import {DialogConfirmDialog} from '../shared/confirm.component';

@Component({
  selector: 'app-guided-search',
  templateUrl: './guided-search.component.html',
  styleUrls: ['./guided-search.component.css']
})
export class GuidedSearchComponent implements OnInit {
  // Filter layout controlor and data
  pageStyle={
    menuNum:null,
    optionTitle:'',
    optionType:null,
    saveBtnFlag:false,
    whichSort:null,
    sortCode:0,
    // searching:false,
    step:160,
    distance:0,
    showL:false,
    showR:false,
    showScrollToTop:false
  };
  // options={GeneralInformation:[],Academic:[],Application:[],Majors:[],Financialaid:[],Athletics:[],Socialexperience:[],TaggedList:[]}
  options=[[],[],[],[],[],[],[],[]];
  optionData=[];
  optionDataTag1={value:[]};
  optionDataTag2=[{value:[]}];

  chipsData=[];
  chipsDatas=[];
  //存放用户所选择的搜索filter相关数据：searchPara为搜索api参数串
  doSearch={
    tempData:[],
    isDoSearch:false,
    compareArr:[],
    sortString:'',
    paraString:'',
    displayName:[],
    chips:[],
    byName:'',
    queryString:[],
    searchPara:'',
    saveTagged:[],
    pageNumber:1,
    canLazy:false
  };
  needLinkOptions=['mensVarsitySports','womensVarsitySports','clubMensSports','clubWomensSports','intramuralSports']
  // studentId=this.auth.userinfo.login_user.id;
  // my matches
  myMatches:any;
  currentSearchList=[]
  // 搜索结果数据
  displayedColumns: string[];
  thDisplay: string[];
  // @ViewChild(MatSort) sort: MatSort;
  searchResaultData=new MatTableDataSource([]);
  resaultTotal:0;
  

  @ViewChild('resultable') canRoll: ElementRef;
  @ViewChildren('select_list') select_list;
  @HostListener("window:scroll", ['$event'])
  scrollMe(event) {
    let table = this.canRoll.nativeElement;
    // let rect = this.canRoll.nativeElement.getBoundingClientRect();
    window.pageYOffset>200?this.pageStyle.showScrollToTop=true:this.pageStyle.showScrollToTop=false
    if(table.clientHeight-window.pageYOffset<window.innerHeight){
      this.doSearch.canLazy?this.lazyLoad():null;
    }
  }
  constructor(
    public collegeService:CollegeService,
    public auth:AuthService,
    public dialog:MatDialog,
    private render:Renderer2,
  ) { 
    this.initFliterData();
  }
  ngOnInit() {
    this.setSearchOptions();
  }
  lazyLoad(){
    this.doSearch.pageNumber+=1;
    this.creatSearchPara();
    this.getMoreResult();
  }
  scrollToTop(){
    scrollTo(0,0);
  }
  reset(){
    this.doSearch.byName='';
    this.doSearch.pageNumber=1;
    this.doSearch.tempData=[];
    this.pageStyle.sortCode=0;
    for(let i=0; i<this.options.length; i++){
      for(let j=0; j<this.options[i].length; j++){
        if(this.options[i][j].value.length>0){
          this.options[i][j].value=[]
        }
        this.options[i][j].name=='Degree'?this.options[i][j].value='B':''; // reset degree to default value
        this.options[i][j].view=false;  // reset compare 
      }
    }
    this.setSearchOptions();
  }
  creatSearchPara(){
    if(this.doSearch.byName){
      this.doSearch.searchPara = 'Name='+this.doSearch.byName + '&' + this.doSearch.paraString +'compare='+ this.doSearch.compareArr.toString();
    }else{
      this.doSearch.searchPara = this.doSearch.paraString+'pageIndex='+this.doSearch.pageNumber +'&compare='+ this.doSearch.compareArr.toString();
    }
    this.doSearch.searchPara=this.doSearch.searchPara+'&sort='+this.doSearch.sortString;
    // console.log(this.options,this.doSearch.searchPara)
  }
  search(){
    let query = this.doSearch.searchPara;
    this.doSearch.canLazy=false;
    
    this.collegeService.getSearchResult(query).then(data=>{
      this.displayedColumns = data.th.valueName;
      this.thDisplay = data.th.displayName;
      this.resaultTotal=data.total;
      // this.resaultTotal>=this.searchResaultData.data.length?this.doSearch.tempData=this.doSearch.tempData.concat(data.data):this.doSearch.tempData=data.data
      this.searchResaultData = new MatTableDataSource(data.data);
      setTimeout(() => {
        // this.searchResaultData.sort = this.sort;
        this.initArrowStatus();
        if(this.resaultTotal>this.searchResaultData.data.length){
          this.doSearch.canLazy=true;
          // console.log(this.searchResaultData.data,data.th)
        }
      });
      
      this.setTaggedListQuery(data);
      this.doSearch.tempData=data.data;
      console.log(data)
      this.resaultTotal==0?this.pageStyle.sortCode=0:null;
    })
  }
  getMoreResult(){
    let query = this.doSearch.searchPara;
    this.doSearch.canLazy=false;
    
    this.collegeService.getSearchResult(query).then(data=>{
      this.displayedColumns = data.th.valueName;
      this.thDisplay = data.th.displayName;
      this.resaultTotal=data.total;
      this.resaultTotal>=this.searchResaultData.data.length?this.doSearch.tempData=this.doSearch.tempData.concat(data.data):this.doSearch.tempData=data.data
      this.searchResaultData = new MatTableDataSource(this.doSearch.tempData);
      setTimeout(() => {
        // this.searchResaultData.sort = this.sort;
        this.initArrowStatus();
        if(this.resaultTotal>this.searchResaultData.data.length){
          this.doSearch.canLazy=true;
        }
      });
      this.setTaggedListQuery(data);
      // console.log(this.searchResaultData.data.length,data.data.length)
    })
  }

  setTaggedListQuery(data){
    this.doSearch.saveTagged=[];
    data.data.map(x=>{
      this.doSearch.saveTagged.push(x.programId)
    })
    if(this.doSearch.saveTagged.length>0){
      this.pageStyle.saveBtnFlag = true;
    }else{
      this.pageStyle.saveBtnFlag = false;
    }
  }

  inputSearch(value){
    this.doSearch.byName='';
    this.doSearch.byName=value;
    this.creatSearchPara();
    this.search();
  }

  // 设置表格所显示表头项参数，并执行搜索
  setCompare(checked:boolean,v:string){
    let arr = this.doSearch.compareArr
    if(checked){
      arr.indexOf(v)!=-1?null:arr.push(v);
    }else{
      arr.indexOf(v)!=-1?arr.splice(arr.indexOf(v),1):null;
    }
    this.creatSearchPara();
    this.search();
  }
  setOrderCompare(arr){
    let order = arr.indexOf('CollegeStyle');
    if(order!=-1){
      arr.splice(order,1);
      arr.unshift('CollegeStyle');
    }
  }
  setSearchOptions(){
    this.doSearch.pageNumber=1;
    this.doSearch.queryString=[];
    
    let arr = this.doSearch.compareArr=[]
    for(let i=0; i<this.options.length; i++){
      for(let j=0; j<this.options[i].length; j++){
        
        if(this.options[i][j].value.length>0){
          this.doSearch.queryString.push(
            {
              name:this.options[i][j].paraName,
              value:this.options[i][j].value.indexOf('selectall')!==-1?this.options[i][j].value.slice(1):this.options[i][j].value
            }
          )
        }
        // set compare string
        if(this.options[i][j].view){  
          arr.indexOf(this.options[i][j].paraName)!==-1?null:arr.push(this.options[i][j].paraName);
        }
      }
    }
    this.setOrderCompare(arr);
    let paraString:string='';
    for(let i=0; i<this.doSearch.queryString.length; i++){
      paraString = paraString + this.doSearch.queryString[i].name +'='+ this.doSearch.queryString[i].value.toString() + '&'
    }
    this.doSearch.paraString = paraString;
    // console.log(paraString);
    this.setChips();
    // Search action ======
    this.creatSearchPara();
    this.search();
  }
  // ---- my Matches 
  myMatchSearch(query){
    let paras=query.split('&');
    let paraName=paras[0].split('=');
    let compare=paras[1].split('=');
    let myMatchOpt=['SATAvgRange','ACTCompRange','GPARange','CollegeStyle','FieldOfStudy']
    for(let i=0; i<this.options.length; i++){
      for(let j=0; j<this.options[i].length; j++){
        let pname = this.options[i][j].paraName;
        myMatchOpt.indexOf(pname)!=-1?this.options[i][j].value=[]:''
        compare[1].indexOf(pname)!=-1?this.options[i][j].view=true:''
        pname==paraName[0]?this.options[i][j].value=paraName[1].split(','):''
      }
    }
    this.setSearchOptions();
    this.switchOptPopup(null);
  }
  // my Matches ---//
  // ---- set filter chips
  setChips(){
    this.chipsDatas=[]
    for(let i=0; i<this.options.length; i++){
      for(let j=0; j<this.options[i].length; j++){
        if(this.options[i][j].value.length>0){
          this.findDisplayName(this.options[i][j],this.options[i][j].value);
        }
      }
    }
    this.doSearch.chips=this.chipsDatas;
  }
  findDisplayName(item,value){
    let inputTpyeItem=['City','MensVarsitySport','WomensVarsitySport','MensClubSport','WomensClubSport','IntramuralSport']
    if(inputTpyeItem.indexOf(item.paraName)!=-1){
      let obj={id:item.id,name:item.name,value:value,displayName:value}
      this.chipsDatas.push(obj);
    }else{
      for(let j=0; j<value.length; j++){
        for(let i=0; i<item.options.length; i++){

          if(item.options[i].value==value[j]){
            let obj={id:item.options[i].id,name:item.name,value:item.value,displayName:item.options[i].name}
            this.chipsDatas.push(obj);
          }
        }
      }
    }
  }
  removeChips(item:any){
    if(item.name=="City"){
      this.options[0][0].value=''
    }else if(item.name=="Degree"){
      this.options[3][0].value=''
    }else{
      for(let i=0; i<this.options.length; i++){
        for(let j=0; j<this.options[i].length; j++){
          if(this.options[i][j].value.indexOf(item.value[0])!=-1){
            this.options[i][j].value.splice(this.options[i][j].value.indexOf(item.value),1)
          }
        }
      }
    }
    this.setSearchOptions();
  }
  // set filter chips -----
  // tooltips
  getTooltipsText(id){
    return id==1?'My Colleges List':id==2?'Interested Colleges List':'Not Interested Colleges List'
  } 
  // seve to my college list
  saveToMyList(type,id){
    this.collegeService.addToMyList(type,id).then(result=>{
      // console.log(result);
      result?this.search():'';
    })
  }
  
  
  // Filter popup
  optPopup(id:any){
    
    switch(id){
      case 1:
        this.pageStyle.optionTitle = 'My Matches';
        this.pageStyle.optionType = 1 ;
        break;
      case 2:
        this.pageStyle.optionTitle = 'Saved Searches';
        this.pageStyle.optionType = 1 ;
        break;
      case 3:
        this.optionData = this.options[0];
        this.pageStyle.optionTitle = 'General Information';
        this.pageStyle.optionType = 2 ;
        //  console.log(this.optionData);
        break;
      case 4:
        this.optionData = this.options[1];
        this.pageStyle.optionTitle = 'Academic';
        this.pageStyle.optionType = 2 ;
        break;
      case 5:
        this.optionData = this.options[2];
        this.pageStyle.optionTitle = 'Application';
        this.pageStyle.optionType = 2 ;
        break;
      case 6:
        this.optionData = this.options[3];
        this.pageStyle.optionTitle = 'Majors';
        this.pageStyle.optionType = 2 ;
        break;
      case 7:
        this.optionData = this.options[5];
        this.pageStyle.optionTitle = 'Athletics';
        this.pageStyle.optionType = 2 ;
        break;
      case 8:
        this.optionData = this.options[4];
        this.pageStyle.optionTitle = 'Financial Aid';
        this.pageStyle.optionType = 2 ;
        break;
      case 9:
        this.optionData = this.options[6];
        this.pageStyle.optionTitle = 'Social Experience';
        this.pageStyle.optionType = 2 ;
        break;
      case 10:
        this.pageStyle.optionTitle = 'Tagged Lists';
        this.pageStyle.optionType = 3 ;
        break;
      default:
        this.pageStyle.optionTitle="";
        this.pageStyle.optionType = null ;
        // this.setSearchOptions();
        this.scrollToTop();
    }
  }
  
  switchOptPopup(id:any){
    this.pageStyle.menuNum==id 
      ? this.pageStyle.menuNum=id=null
      : this.pageStyle.menuNum=id;
    this.optPopup(id);
  }
  selectAll(id){
    let isAll:boolean=this.select_list._results[id].options.first._selected;
    let data = this.optionData;
    
    if(isAll){
      data[id+1].value=['selectall']
      data[id+1].options.map(
          x=>{
            
            data[id+1].value.push(x.value);
            
          }
        )
    }else{
      data[id+1].value=[]
    }
     this.setSearchOptions();
    // console.log(this.optionData[id+1].value);
  }
  selectOne(id){

    let vLength=this.optionData[id+1].value.length;
    let oLength=this.optionData[id+1].options.length;
    let v=this.optionData[id+1].value

    if(v.indexOf('selectall')!=-1 && vLength<=oLength){
      v.splice(v.indexOf('selectall'),1);
      this.select_list._results[id].options.first._selected=false;
    }else if(v.indexOf('selectall')==-1 && vLength==oLength){
      this.select_list._results[id].options.first._selected=true;
      v.unshift('selectall');
    }
    // console.log(this.optionData[id+1].value);
     this.setSearchOptions();
  }
  sort(item,id){
    
    this.pageStyle.whichSort!=id 
      ? (this.pageStyle.whichSort=id, this.pageStyle.sortCode=0)
      : null
      
    if(this.pageStyle.sortCode==0){
      this.pageStyle.sortCode=1;
      this.doSearch.sortString=item+","+this.pageStyle.sortCode;
      // console.log("sort="+item+","+this.pageStyle.sortCode)
    }else if(this.pageStyle.sortCode==1){
      this.pageStyle.sortCode=2;
      this.doSearch.sortString=item+","+this.pageStyle.sortCode;
      // console.log("sort="+item+","+this.pageStyle.sortCode)
    }else if(this.pageStyle.sortCode==2){
      this.pageStyle.sortCode=0
      this.doSearch.sortString='';
      // console.log("sort=")
    }
    this.setSearchOptions();
  }
  initFliterData(){
    this.collegeService.getFlterInfo('GeneralInformation/').then(data=>{
      this.options[0] = data.GeneralInformation;
    });
    this.collegeService.getFlterInfo('Academic/').then(data=>{
      this.options[1] = data.Academic;
    });
    this.collegeService.getFlterInfo('Application/').then(data=>{
      this.options[2] = data.Application;
    });
    this.collegeService.getFlterInfo('Majors/').then(data=>{
      // let all={id:99999,name:'Select All',value:false}
      for(let i=1; i<data.Majors.length; i++){
        data.Majors[i].state="selectall";
        // data.Majors[i].id==1458?data.Majors.splice(i,1):null;
      }
      this.options[3] = data.Majors;
      // console.log(this.options[3]);
    });
    this.collegeService.getFlterInfo('Financialaid/').then(data=>{
      this.options[4] = data.Financialaid;
    });
    this.collegeService.getFlterInfo('Athletics/').then(data=>{
      this.options[5] = data.Athletics
    });
    this.collegeService.getFlterInfo('Socialexperience/').then(data=>{
      this.options[6] = data.Socialexperience
    });
    this.collegeService.getFlterInfo('TaggedList/').then(data=>{
      this.options[7] = data.TaggedList;
      this.optionDataTag1 = this.options[7][0];
      this.optionDataTag2 = this.options[7].slice(1,);
    });
    this.collegeService.getMyMatches().then(result=>{
      this.myMatches=result;
    });
    this.getCurrentSearch();
  }
  keydownSearch(){
    setTimeout(()=>{this.setSearchOptions()});
  }
// Save Tagged List
  saveTaggedList(query){
    this.collegeService.saveTaggedList(query).then(result=>{
      if(result.status==0){this.openDialog(1,result.msg)}
      this.initFliterData();
    })
  }
// Tagged search
  taggedSearch(istrue,value,which){
    if(which==null){
      istrue?this.optionDataTag1.value.push(value):this.optionDataTag1.value.splice(this.optionDataTag1.value.indexOf(value),1)
    }else{
      istrue?this.optionDataTag2[which].value.push(value):this.optionDataTag2[which].value.splice(this.optionDataTag2[which].value.indexOf(value),1)
    }
    this.setSearchOptions();
    // console.log(value,which,this.optionDataTag1,this.optionDataTag2);
  }
  getStateOfTag(val){
    for(let i=0; i<this.options[7].length; i++){
      if(this.options[7][i].value.indexOf(val)!=-1){return true}
    }
    return false;
  }
// Saved Searches list----
  doSavedSearch(query){
   
    let paras=query.split('&');
    let pName=[];
    let pValue=[];
    const paraMap = paras.map(x=>{
      pName.push(x.split('=')[0]);
      let l=x.split('=')[1].split(',').length;
      // transport to number
        let values = x.split('=')[1].split(',').map(item=>{
          let temp = parseInt(item);
          
          if (temp === temp) {
            return temp;
          } else {
            return item;
          }
        });
      
      pValue.push(values);
      
    })
    
    for(let i=0; i<this.options.length; i++){
      for(let j=0; j<this.options[i].length; j++){
        
        let pname = this.options[i][j].paraName;
        pValue[pValue.length-1].indexOf(pname)!=-1?this.options[i][j].view=true:''
        pName.indexOf(pname)!=-1?this.options[i][j].value=pValue[pName.indexOf(pname)]:'';
        
      }
    }
    // console.log(this.options,pName,pValue);
    this.setSearchOptions();
    this.switchOptPopup(null);
  }
  
  getCurrentSearch(){
    this.collegeService.getCurrentSearch().then(result=>{
      this.currentSearchList=result;
    })
  }
  saveCurrentSearch(query){
    this.collegeService.saveCurrentSearch(query).then(result=>{
      this.getCurrentSearch();
    })
  }
  editCurrentSearch(id,query){
    this.collegeService.editCurrentSearch(id,query).then(result=>{
      this.getCurrentSearch();
    })
  }
  deleteCurrentSearch(id){
    this.collegeService.deleteCurrentSearch(id).then(result=>{
      this.getCurrentSearch();
    })
  }
  openConfirm(e):void{
    const dialogRef = this.dialog.open(DialogConfirmDialog, {
      width: '400px',
      data: {name:e.name,id:e.id}
    });

    dialogRef.afterClosed().subscribe(result => {
      result?this.deleteCurrentSearch(result):console.log('false')
    });
  }
  openEditSavedSearchDialog(e): void{
    const dialogRef = this.dialog.open(DialogSvaeCurrentSearchDialog, {
      width: '500px',
      data: {name:e.name,description:e.description,id:e.id}
      
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result){
        let query={data:{name:result.name,description:result.description}}
        this.editCurrentSearch(result.id,query);
      }
    });
  }
  
  openDialog(which,err=null): void {
    let title="";
    which==0?title="Save Your Current Search":title="Save Results as Tagged List";
    const dialogRef = this.dialog.open(DialogSvaeCurrentSearchDialog, {
      width: '500px',
      data: {title:title,err:err,name:'',description:'',query:this.doSearch.searchPara,data:this.doSearch.saveTagged,which:which,typeID:1}
      
    });
console.log(this.doSearch.searchPara);
    dialogRef.afterClosed().subscribe(result => {
      if(result){
        
        if(result.which=='0'){
          let query={name:result.name,query:result.query,description:result.description}
          this.saveCurrentSearch(query);
        }else{
          let query={name:result.name,data:result.data,description:result.description,typeID:result.typeID}
          this.saveTaggedList(query);
        }
      };
      
    });
  }
  // Saved Searches list ----
  //  ----tabel scroll controler
  initArrowStatus(){
    let tabel = this.canRoll.nativeElement;
    if(tabel.scrollWidth>tabel.clientWidth){
      tabel.scrollLeft==0 ? this.pageStyle.showL=false : this.pageStyle.showL=true;
      tabel.scrollLeft==tabel.scrollWidth-tabel.clientWidth ? this.pageStyle.showR=false : this.pageStyle.showR=true
    }else{
      this.pageStyle.showL = this.pageStyle.showR = false
    }
  }
  hScroll(e){
    let tabel = this.canRoll.nativeElement;
    this.pageStyle.distance += e*this.pageStyle.step 
    tabel.scrollLeft=this.pageStyle.distance;
    this.initArrowStatus();
  }
  //  tabel scroll controler----
  needLink(name){
    console.log(name);
    return this.needLinkOptions.indexOf(name)!=-1;
  }
}

@Component({
  selector: 'dialog-save-current-search-dialog',
  template: `
<h3 mat-dialog-title class="text-center">{{data.title}}</h3>
<div mat-dialog-content class="row">
  <div class="text-danger mb-4" >{{data.err}}</div>
  <div>
    <mat-form-field class="w-100">
    <input matInput [(ngModel)]="data.name" placeholder="Enter a name" [formControl]="name" required>
    <mat-error *ngIf="name.invalid">Take a name for your search</mat-error>
    </mat-form-field>
  </div>
  <mat-form-field *ngIf="data.which=='1'">
  <mat-select [(value)]="data.typeID" matNativeControl required>
    <mat-option [value]="1">General</mat-option>
    <mat-option [value]="2">Academic</mat-option>
    <mat-option [value]="3">Financial</mat-option>
    <mat-option [value]="4">Social</mat-option>
  </mat-select>
</mat-form-field>
  <div class="py-2">Description:</div>
  <div class="pb-2"><textarea class="w-100" [(ngModel)]="data.description" placeholder="Enter a description (optional)"></textarea></div>
</div>
<div mat-dialog-actions class="d-flex justify-content-around">
  <button mat-button  (click)="onNoClick()">Cancel</button> <button mat-button color="primary"  [mat-dialog-close]="data"  [disabled]="name.invalid">Save</button>
</div>
`,
})
export class DialogSvaeCurrentSearchDialog {
  name = new FormControl('', [Validators.required]);

  constructor(
    public dialogRef: MatDialogRef<DialogSvaeCurrentSearchDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
  
}
export interface DialogData {
  id:string;
  title:string;
  name:string;
  query:string;
  data:any;
  description:string;
  flag:boolean;
  typeID:string;
  which:string;
  err:string;
}