import { Component, OnInit, Inject, Output } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';

@Component({
    selector: 'dialog-confirm-dialog',
    template: `
    <h3 mat-dialog-title class="text-center">Are you sure?</h3>
    <div mat-dialog-content>
    <p>This action will <b>DELETE</b> "{{data.name}}" from your list</p>
    
  </div>
  <div mat-dialog-actions class="d-flex justify-content-between">
    <button mat-button  (click)="onNoClick()">Cancel</button> <button mat-button color="primary"  [mat-dialog-close]="data.id">Confirm</button>
  </div>
  `,
  })
  export class DialogConfirmDialog {
  
    constructor(
      public dialogRef: MatDialogRef<DialogConfirmDialog>,
      @Inject(MAT_DIALOG_DATA) public data: DialogData) {}
  
    onNoClick(): void {
      this.dialogRef.close();
    }
  
  }
  export interface DialogData {
    id:number;
    name:string;
  }