import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute,NavigationEnd } from '@angular/router';
import { AuthService } from '../service/auth.service';
import { filter,take } from 'rxjs/operators';

@Component({
  selector: 'app-college-layout',
  templateUrl: '../customer.component.html',
  styleUrls: ['../customer.component.css'],
  providers: [],
})
export class CollegeComponent implements OnInit {
  show_header = true;
  show_sidebar = true;
  title = 'GuidedPath Customer';
  
  constructor(public router: Router,public auth: AuthService,private route: ActivatedRoute) {
    this.show_header=this.router.url.substring(9,13)=='info'?false:true;
    this.show_sidebar=this.router.url.substring(9,13)=='info'?false:true;
    this.router.events.subscribe(data => {
      if (data instanceof NavigationEnd) {
        this.show_header=data.url.substring(9,13)=='info'?false:true;
        this.show_sidebar=this.router.url.substring(9,13)=='info'?false:true;
      }
    });
  }

  ngOnInit() {
    
  }
}
