import { Component, OnInit, Inject, Input } from '@angular/core';
import { DndDropEvent, DropEffect } from 'ngx-drag-drop';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { AuthService } from '../../service/auth.service';
import { CollegeService } from '../../service/college.service';
import {DialogConfirmDialog} from '../shared/confirm.component';
import {CdkDragDrop, moveItemInArray, transferArrayItem} from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-college-list',
  templateUrl: './college-list.component.html',
  styleUrls: ['./college-list.component.css']
})
export class CollegeListComponent implements OnInit {
  @Input() reset:string;
  studentId=this.auth.userinfo.login_user.id;
  currentItem={id:'',toTypeID:null,toRank:null};
  inputID='';
  savedFlag=0;

  draggableListLeft = {
    id:1,
    data:[]};
  draggableListMiddle = {
    id:2,
    data:[]};
  draggableListRight = {
    id:3,
    data:[]};

  layout:any={
    container: "row",
    list: "column",
    dndHorizontal: false
  };
  btnDisable:any={
    btn:true,
    alert:false
  }
  // horizontalLayoutActive:boolean = false;
  private currentDraggableEvent:DragEvent;
  private currentDragEffectMsg:string;
  

  constructor( 
    public cs:CollegeService,
    public dialog:MatDialog,
    public auth:AuthService
  ) {
    this.initData();

  }
  drop(event: CdkDragDrop<any[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
      this.findPostData(event.container.data[event.currentIndex].ID,event.currentIndex+1);
      console.log(event.container.data[event.currentIndex].ID);
    } else {
      transferArrayItem(
        event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex);
      this.findPostData(event.container.data[event.currentIndex].ID,event.currentIndex+1);
      console.log(event);
    }
  }
  ngOnInit() {
    
  }
  getid(e:any){
    this.inputID=e;
    this.isDisabledButton();
    console.log(this.inputID)
  }
  addToMyList(type,id){
    
    this.cs.addToMyList(type,id).then(result=>{
      
      result?this.initData():this.btnDisable.alert=true;setTimeout(()=>{this.btnDisable.alert=false},3000);
    })
  }
  
  removeFormList(id){
    this.cs.removeFormMyCollegeList(id).then(result=>{
      this.initData();
    })
  }
  changExpect(e,id){
    let post={expectation:e.value}
    this.cs.editCollege(id,post).then(result=>{
      // this.savedFlag.loading=false;
      result?this.savedFlag=id:''
      setTimeout(() => {
        this.savedFlag=0
      }, 1500);
    })
  }
// 将接口数据整理成所需结构；
  objToArray(data){
    let arr=[];
    if(data){
      let l = Object.keys(data);
      for(let i=0; i<l.length; i++){
        arr.push(data[l[i]]);
      }
    }
    
    return( arr );
  }
  gotoUrl(){
    window.open('/college/info/'+this.inputID,'_blank')
  }
  isDisabledButton(){
    if(this.inputID){
      this.btnDisable.btn=false;
      for(let i=0; i<this.draggableListLeft.data.length; i++){
        if(this.draggableListLeft.data[i].programID==this.inputID){
          this.btnDisable.btn=true;
          console.log(this.draggableListLeft.data[i].programID);
        }
      }
      for(let i=0; i<this.draggableListMiddle.data.length; i++){
        if(this.draggableListMiddle.data[i].programID==this.inputID){
          this.btnDisable.btn=true;
        console.log(this.draggableListMiddle.data[i].programID);
        }
      }
      console.log(this.btnDisable.btn,this.btnDisable.alert);
    }else{
      this.btnDisable.btn=true;
    }
  }
  onDragStart( event:DragEvent ) {

    this.currentDragEffectMsg = "";
    this.currentDraggableEvent = event;
  }

  onDragged( item:any, list:any[], effect:DropEffect ) {

    this.currentDragEffectMsg = `Drag ended with effect "${effect}"!`;
     console.log(item);
    if( effect === "move" ) {
      const index = list.indexOf( item );
      list.splice( index, 1 );
    }
     
  }

  onDragEnd( event:DragEvent) {
    this.currentDraggableEvent = event;
    this.findPostData(this.currentItem.id,this.currentItem.toRank);
    console.log(event);
  }

  onDrop( event:DndDropEvent, list?:any[] ) {

    if( list &&  event.dropEffect === "move" ) {

      let index = event.index;

      if( typeof index === "undefined" ) {
        index = list.length;
      }

    list.splice( index, 0, event.data);
    this.currentItem.id=event.data.ID;
    this.currentItem.toRank=index+1;
     
     console.log(event);
      // console.log(this.draggableListLeft,this.draggableListMiddle,this.draggableListRight);
    }
  }
  // studentID=1229&id=203&toTypeID=1&toRank=2
  findPostData(id,index){
    this.draggableListLeft.data.findIndex(function(item){ return item.ID == id })!=-1?  
      this.currentItem={'id':id,'toTypeID':1,'toRank':index}:'';

    this.draggableListMiddle.data.findIndex(function(item){ return item.ID == id })!=-1?  
      this.currentItem={'id':id,'toTypeID':2,'toRank':index}:'';

    this.draggableListRight.data.findIndex(function(item){ return item.ID == id })!=-1?  
      this.currentItem={'id':id,'toTypeID':3,'toRank':index}:'';
    this.cs.collegeListMoveCollege(this.currentItem.id,this.currentItem.toTypeID,this.currentItem.toRank).then(result=>{
      console.log( result );
    }).catch(err=>{
      console.log(err)
    })
  }

  initData(){
    this.cs.getCollegeList().then(result=>{
      this.draggableListLeft.data = this.objToArray(result.data[1]);
      this.draggableListMiddle.data = this.objToArray(result.data[2]);
      this.draggableListRight.data = this.objToArray(result.data[3]);
      console.log(this.draggableListLeft.data)
    })
  }
  
  openDialog(which,e): void {
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: '300px',
      data: {which:which, name:e.name, publicPrivate: e.publicPrivate, undergradStudentsValue: e.undergradStudentsValue, acceptanceRateValue:e.acceptanceRateValue ,locationType:e.locationType,title:"note",msg:e.name}
      
    });

    dialogRef.afterClosed().subscribe(result => {
      
    });
  }

  openConfirm(e):void{
    const dialogRef = this.dialog.open(DialogConfirmDialog, {
      width: '400px',
      data: {name:e.name,id:e.ID}
    });
      console.log(e);

    dialogRef.afterClosed().subscribe(result => {
      
      result?this.removeFormList(result):''

    });
  }
}
@Component({
  selector: 'dialog-overview-example-dialog',
  template: `
<div class="w-100 d-flex justify-content-end"><i class="material-icons" style="cursor:pointer" (click)="onNoClick()">
  close
  </i></div>
<h3 mat-dialog-title>{{data.which==0?data.name:data.title}}</h3>
<div mat-dialog-content *ngIf="data.which==0">
  <p>{{data.publicPrivate}}</p>
  <p>Undergraduate Size: {{data.undergradStudentsValue | number: '2.'}}</p>
  <p>Acceptance Rate: {{data.acceptanceRateValue}}</p>
  <p>{{data.locationType}}</p>
</div>
<div mat-dialog-content *ngIf="data.which==1">
  <p>{{data.msg}}</p>
</div>
`,
})
export class DialogOverviewExampleDialog {

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}
export interface DialogData {
  ID:number;
  which:number;
  name:string;
  publicPrivate:string;
  undergradStudentsValue:string;
  acceptanceRateValue:string;
  locationType:string;
  title:string;
  msg:string;
}