import { Injectable } from '@angular/core';
import { FactoryService } from './factory.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { CollegeService } from "./college.service";

@Injectable({
  providedIn: 'root'
})
export class ChartService extends FactoryService {
  chart:any={
    barcolors:['#6db910','#f9d91c','#bf1201','#ffa500','#0000ff'],
    maincolors:['#97c846','#105f95'],
    colors:['#65b7ec','#1370ef', '#8f3faf', '#00c3e1', '#f2c300', '#83c971'],
    legendcolor:'#515974',
    credit:{
      enabled:false,
      href:'https://www.guidedpath.net',
      position:{align:'right',
        verticalAlign:'bottom',
        x:-10,
        y:-5},
      style:{"cursor": "pointer", "color": "#999999", "fontSize": "10px"},
      text:'GuidedPath.net',
    },
  }
  disableClick:any={
    legendItemClick: function () {
      return false;
    }
  }
// dev-data
dashbooard:any={
  myCollegeList:{},
  myChances:{},
  myCollegeUndergraduateSize:{},
  interestedCollegeUndergraduateSize:{},
  netPriceRatings:{},
};

myChancesData:any={
    
    //act Chart Data
    actChart:{
      title:'My ACT Score Comparison',
      categories: ['Composite', 'English', 'Math']
    },
    actTable:{
      title:"ACT - Enrolled Freshman Score Distribution",
      label:["","0-11","12-18","19-24","25-30","31-36","My Scores","College Middle 50%"],
      data:[
        {label:"Composite",a:"0%",b:"7%",c:"42%",d:"43%",e:"8%",myScores:"34",collegeMiddle:"21 - 26"},
        {label:"English",a:"0%",b:"7%",c:"42%",d:"43%",e:"8%",myScores:"32",collegeMiddle:"20 - 27"},
        {label:"Math",a:"0%",b:"7%",c:"42%",d:"43%",e:"8%",myScores:"35",collegeMiddle:"21 - 26"}
      ]
    },
    //sat Chart Data
    satChart:{
      title:'My SAT Score Comparison',
      categories: ['EB Reading<br> and Writing', 'Math']
    },
    satTable:{
      title:"SAT - Enrolled Freshman Score Distribution",
      label:["","200-399","400-499","500-599","600-699","700-800","My Scores","College Middle 50%"],
      data:[
        {label:"EB Reading and Writing",a:"0%",b:"7%",c:"42%",d:"43%",e:"8%",myScores:"734",collegeMiddle:"540 - 640"},
        {label:"Math",a:"0%",b:"7%",c:"42%",d:"43%",e:"8%",myScores:"732",collegeMiddle:"530 - 620"}
      ]
    }
  
}
  

//end of dev-data

  constructor(protected http: HttpClient,public collegeService:CollegeService) {
    super(http);
  }

}
